.app-wrapper {
  display: grid;
  width: 1201px;
  margin: 0 auto;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  border: 1px solid #3f4257;
  
  
}
.app-wrapper-content {
  grid-area: c;
  background-color: #edf2f6;
  width: 1000px;

}





